* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  background-color: black;
  color: snow;
  font-family: Arial, sans-serif;
}
.mainBox {
  max-width: 90%;
  height: auto;
  margin: 0 auto;
  text-align: center;
  margin-top: 5vw;
}
.mainBox h1 {
  font-size: 3em;
  margin-bottom: 20px;
}
.mainBox .passSection {
  width: 100%;
  margin: 0 auto;
  margin-top: 30px;
}
.passSection input {
  width: 80%;
  max-width: 600px;
  height: 50px;
  padding-left: 1em;
  font-size: 1.5rem;
  border-radius: 10px;
  margin-bottom: 20px;
}
input:focus {
  outline: none;
}
.passSection button {
  width: auto;
  margin-left: 20px;
  padding: 0 20px;
  font-size: 1.5rem;
  border-radius: 10px;
  height: 50px;
  font-weight: 700;
  border: none;
  cursor: pointer;
  background-image: linear-gradient(red, rgb(255, 170, 0));
}
.additional {
  width: 90%;
  margin: 0 auto;
  /* display: flex; */
  /* flex-direction: column; */
  /* align-items: center; */
  margin-top: 20px;
}
.additional .range {
  width: 10%;
  margin: 10px 0;
}
.additional label {
  font-weight: 800;
  font-size: 1.2rem;
  margin: 15px 15px;
}
.additional #inputNum,
#inputchar {
  margin-top: 15px;
  margin-left: 25px;
  transform: scale(2.5);
  border: none;
  cursor: pointer;
}
@media (max-width: 988px) {
  .mainBox h1 {
    font-size: 4em;
  }
  .additional .range {
    width: 30%;
    /* margin: 10px 0; */
  }
  .passSection input {
    height: 60px;
    font-size: 2rem;
  }
  .passSection button {
    height: 60px;
    font-size: 2rem;
  }
  .additional {
    align-items: center;
    display: flex;
    flex-direction: column;
    margin-top: 1vw;
  }
  .additional label {
    font-size: 1.5rem;
  }
}
